import React, { Component } from 'react';
import { IonContent, IonPage, IonRow, IonCol, IonCard, IonCardContent, IonAlert, IonHeader, IonList, IonItem, IonLabel, IonInput, IonRange, IonButton, IonCardHeader, IonCardTitle } from '@ionic/react';
import './Manage.scss';
import ImageCapture from 'app/components/ImageCapture/ImageCapture';
import Map from "app/components/shared/Map1";

class ManagePage extends Component<any, any> {

    lat: any;
    long: any;

    state = {
        geofenceIn: false,
        geofenceLant: 0.0,
        geofenceLong: 0.0,
        radius: 0.5,
        // currentLatitude: 0.00,
        // currentLongitude: 0.00,
        distance: 0,
        geofenceaddr: ''
    }

    handleSubmit = (event: any) => {
        // console.log("state:::" + JSON.stringify(this.state));
        // this.inputLang = parseFloat(this.state.geofenceLant);
        // this.inputLong = parseFloat(this.state.geofenceLong);

        let lat1: any; 
        let long1: any;

        var directionsRequest:any = {
            origin: this.state.geofenceaddr, 
            destination: this.state.geofenceaddr,
            provideRouteAlternatives: false,
            travelMode: 'DRIVING',
            unitSystem: google.maps.UnitSystem.IMPERIAL
          };
        
          var directionsService = new google.maps.DirectionsService();
          directionsService.route(directionsRequest, function(response:any, status:any) {
            if (status == google.maps.DirectionsStatus.OK) {
              var myRoute = response.routes[0].legs;
               console.log("latitude::::"+ myRoute[0].start_location.lat() + "longitude::::"+ myRoute[0].start_location.lng());
               lat1 = myRoute[0].start_location.lat();
               long1 = myRoute[0].start_location.lng();
               
               
        
            }
          });

          console.log("lat1:::"+lat1 +"   long1:::"+long1);
    }

    handleChange = (event: any) => {

        const { name, value } = event.target;
        // console.log("  name::: " + name + "   value:::: " + value);
        this.setState({
            [name]: value
        });

    }

    render() {
        const { manageCard } = this.props;
        return (
            <IonPage className="manage-header">
                 <IonHeader className="profileHeader manageHeader">
                     {/* <img alt="profile" src={this.state.imagePath} className="profileImg" onClick={this.takePicture}></img> */}
                     {/* <IonBadge className="editBadge">
                         <img alt="profile" src="assets/images/Edit.png" className="profileEditIcon" ></img>
                     </IonBadge> */}

                     <ImageCapture   {...this.props} />
                     <div className="profileName">Igor Smith</div>
                     <div className="Dispatcher">Dispatcher</div>
                     <div className="profileEmail">igor@schneider.com</div>
                 </IonHeader>
                 <IonContent>
                     <div className="card_content_background">
                         <div className="cardContent-space">
                             {manageCard.length>0?manageCard.map((profileData: any, index: number) => (
                                 <IonCard className="profileCard" routerLink={`${profileData.profileRouting}`} key={index}>
                                     <IonCardContent className="profile-cardContent">
                                         <IonRow className="profile-card-row">
                                             <IonCol size='2'>
                                                 <img alt="profile" src={profileData.cradImg} className="profileIcon"></img>
                                             </IonCol>
                                             <IonCol size='9'>
                                                 <p className="carrier-profile">{profileData.profileHeading}</p>
                                             </IonCol>
                                             <IonCol size='1' className="manage-profile-arrow-col">
                                                 <div className="arrow_forward"></div>
                                             </IonCol>
                                         </IonRow>
                                     </IonCardContent>
                                 </IonCard>
                             )):null}
                         </div>

                     </div>
                 </IonContent>
             </IonPage>
        )
    }
}
export default ManagePage;