import React, { Component } from 'react';

import { Plugins,MotionEventResult } from '@capacitor/core';

const { Motion, Geolocation } = Plugins;

export class Acclerometer extends Component<any, any>{

    constructor(props: any) {

        super(props)

        this.state = {

            X: 0,

            Y: 0,

            Z: 0,

            u: 0,

            gps: {},
            motion: {}
        }

    }

    componentDidMount() {
        //const { X, Y, Z } = this.state;

        Motion.addListener('accel', async (result: MotionEventResult) => {
            this.setState({ motion: { first: Math.round(Math.abs(result.acceleration.x * 2.236936)), second: Math.round(Math.abs(((result.acceleration.x) * 2.236936 / 250) * 180)) } });
        })


        var watch = Geolocation.watchPosition(
            { enableHighAccuracy: true },
            (result: any, error: any) => {
                if (result) {
                    this.setState({ gps: { first: Math.round(Math.abs(result.coords.speed * 2.236936)), second: Math.round(Math.abs(((result.coords.speed) * 2.236936 / 250) * 180)) } });
                } else {
                    console.log('GPS Error', error);
                }
            }
        );
    }

    render() {
        return (
            <div style={{margin:"10px"}}>
                <h3>IONIC Capacitor Accelerometer </h3>
                <div>-------------------------------------------------</div>
                <div>motion validation-1 for speed : </div>
                <div style={{fontSize:"180px", backgroundColor:(this.state.motion.first>40?'red':'transparent')}}>{this.state.motion.first} <span style={{fontSize:"50px"}}>MPH</span></div>
                <br />
                <div>motion for validation-2 for speed : </div>
                <div style={{fontSize:"180px", backgroundColor:(this.state.motion.first>40?'red':'transparent')}}>{this.state.motion.second} <span style={{fontSize:"50px"}}>MPH</span> </div>
                <br />
                {/* <br />
                <h3>IONIC Capacitor gpslocation </h3>
                <div>-------------------------------------------------</div>
                <div>gps validation-1 for speed : </div>
                <div>{this.state.gps.first} MPH </div>
                <br />
                <div>gps validation-2 for speed : </div>
                <div>{this.state.gps.second} MPH </div> */}
            </div>

        )

    }

}

export default Acclerometer;